const num = /^\+?[1-9][0-9]*$/;  //0
const numZ = /^\+?[0-9][0-9]*$/; //(0)
const numZ2 = /^-?[0-9][0-9]*$/; //(0)
const numF = /^-?([0-9][0-9]*)(\.\d{1,2})?$/; //(0,)
const numF2 = /^([0-9][0-9]*)(\.\d{1,2})?$/; //(,)
const numD = /^-?([0-9][0-9]*)(\.\d{1,10})?$/; //(0,10)
const numD2 = /^(([0-9]+[\.]?[0-9]+)|[1-9])$/; //(0,)
const numD3 = /^(([0-9]+[\.]?[0-9]+)|[0-9])$/; //(,)
const num0_100 = /^(?:0|[1-9][0-9]?|100)$/;  //0-100
const num0_1 = /^[01]$|^0\.\d{1,2}$|^1\.0{1,2}$/;  //0-1(),01
const post = /^\d{6}$/; //
const phone = /^(((\()?\d{2,4}(\))?[-(\s)*]){0,2})?(\d{7,8})$/; //(2-4,,,1-2;7-8)
const mobile = /^[1][3,5,8][0-9]{9}$/; //
const email = /^[0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*@(([0-9a-zA-Z])+([-\w]*[0-9a-zA-Z])*\.)+[a-zA-Z]{2,9}$/; //Email(@)
const date = /^\d{4}-\d{2}-\d{2}$/;  //yyyy-MM-dd
const time = /^\d{2}:\d{2}:\d{2}$/;  //hh:mm:ss
const datetime = /^\d{4}-\d{2}-\d{2} \d{2}:\d{2}:\d{2}$/;  //yyyy-MM-dd hh:mm:ss
const datetimeO = /^\d{4}-\d{2}-\d{2}( \d{2}:\d{2}:\d{2})?$/;  //datedatetime
const pass = /^[0-9a-zA-Z]{6,16}$/;  //,6-16
const user = /^[a-zA-Z][a-zA-Z0-9_\u4E00-\u9FA5]{3,15}$/;  //,,4-16
const ip = /^((25[0-5])|(2[0-4]\d)|(1\d\d)|([1-9]\d)|\d)(\.((25[0-5])|(2[0-4]\d)|(1\d\d)|([1-9]\d)|\d)){3}(:(6553[0-5]|655[0-2]\d|65[0-4]\d{2}|6[0-4]\d{3}|[1-5]\d{4}|[1-9]\d{0,3}))?$/; //IPxxx.xxx.xxx.xxxxxx.xxx.xxx.xxx:,xxx0-255,1-65535
const cardId = /^(\d{18,18}|\d{15,15}|\d{17,17}x)$/i;   //
const enFirst = /^[a-zA-Z][\s\S]*$/;  //
const chFirst = /^[\u4E00-\u9FA5][\s\S]*$/;  //
const percent = /^\+?(([1-9]\d?)|(100)|(0))\%$/;  //(0%-100%)

export {
  num,
  numZ,
  numZ2,
  numF,
  numF2,
  numD,
  numD2,
  numD3,
  num0_100, 
  num0_1,
  post,
  phone,
  mobile,
  email,
  date,
  time,
  datetime, 
  datetimeO,
  pass,
  user,
  ip,
  cardId,
  enFirst,
  chFirst,
  percent
};


