import { Component, PropTypes } from 'react';
import { registerTmpl, registerFilter } from 'nornj';
import classNames from 'classnames';
import Widget from '../widget';
import * as utils from '../../utils';
import tmpl from './pagination.t.html';

/**
 * 
 */
function getPageCount(count, pageSize) {
  return parseInt(count % pageSize > 0 ? count / pageSize + 1 : count / pageSize, 10);
}

/**
 * 
 */
@registerTmpl({
  name: 'fj-Pagination',
  template: tmpl.pagination
})
class Pagination extends Widget {
  static defaultProps = {
    fjType: 'pagn',
    pageSize: 15,                           //
    pageSizes: [15, 30, 50],                //
    pageIndex: 1,                           //,1
    pageCountPrefix: '',
    pageCountSuffix: '',
    count: 0,                               //
    countPrefix: '',
    countSuffix: '',
    sizePrefix: '',
    sizeSuffix: '',
    btnGoName: '',                      //
    noCount: false,                         //true
    setPageSize: false,                     //
    showCount: true,                        //
    showPageSize: true,                     //
    showPageCount: true,                    //
    showRefresh: true,
    hasPages: true,                         //
    hasBtnGo: true,
    emptyText: '',
    responsive: false,
    responsiveDelay: 70,
    responsiveOnlyWidth: true,
    defaultResponsiveParam: {               //
      '(max-width: 480px)': {
        state: {
          showCount: false,
          showPageSize: false,
          hasPages: false,
          hasBtnGo: false
        }
      },
      '(min-width: 481px) and (max-width: 768px)': {
        state: {
          showCount: false,
          showPageSize: false,
          hasPages: true,
          hasBtnGo: true
        }
      },
      '(min-width: 769px)': {
        state: {
          showCount: true,
          showPageSize: true,
          hasPages: true,
          hasBtnGo: true
        }
      }
    }
  };
  
  constructor(props) {
    super(props, {
      pageIndex: parseInt(props.pageIndex, 10),
      pageSize: parseInt(props.pageSize, 10)
    });
  }

  init() {
    this.state.pageCount = this.getPageCount()  //
    super.init();

    this.pageSizeChange = this.pageSizeChange.bind(this);
    this.pageIndexBlur = this.pageIndexBlur.bind(this);
    this.setGoPage = this.setGoPage.bind(this);
    this.goPage = this.goPage.bind(this);
    this.refresh = this.refresh.bind(this);
  }

  componentWillReceiveProps (nextProps) {
    let { pageIndex, count, pageSize } = this.props;
    pageIndex = parseInt(pageIndex, 10);
    pageSize = parseInt(pageSize, 10);
    count = parseInt(count, 10);

    let {
      pageIndex: indexN,
      count: countN,
      pageSize: pageSizeN } = nextProps;
    if(indexN != null) {
      indexN = parseInt(indexN, 10);
    }
    if(pageSizeN != null) {
      pageSizeN = parseInt(pageSizeN, 10);
    }
    if(countN != null) {
      countN = parseInt(countN, 10);
    }

    let newState = {},
      isSetState = false;

    if (indexN !== pageIndex) {
      isSetState = true;
      newState.pageIndex = indexN;
      this.setGoPage(indexN);
    }
    if (pageSizeN !== pageSize || countN !== count) {
      isSetState = true;
      newState.pageSize = pageSizeN != null ? pageSizeN : this.state.pageSize;
      newState.pageCount = this.getPageCount(newState.pageSize, countN);
    }

    if(isSetState) {
      this.setState(newState);
    }
  }

  componentWillMount () {
    //
    this.refresh();
  }

  getPageCount(pageSize = this.state.pageSize, count = this.props.count) {
    return getPageCount(count, pageSize);
  }

  //
  pageSizeChange(pageSize) {
    this.refresh(this.state.pageIndex, parseInt(pageSize, 10));
  }

  //
  pageIndexBlur(e) {
    if(!utils.RegExp.num.test(e.target.value.trim())) {
      e.target.value = 1;
    }
  }

  //
  setGoPage(pageIndex) {
    if(this.refs.pageTxt) {
      this.refs.pageTxt.value = pageIndex;
    }
  }

  //
  refresh(pageIndex = this.state.pageIndex, pageSize = this.state.pageSize) {
    let props = this.props,
      pageCount = this.getPageCount(pageSize);

    //,
    if(pageIndex > pageCount) {
      pageIndex = pageCount;
    }
    if(pageIndex < 1) {
      pageIndex = 1;
    }

    this.setGoPage(pageIndex);
    this.setState({
      pageIndex: pageIndex,
      pageSize: pageSize,
      pageCount: pageCount
    });

    if(props.onChange) {
      props.onChange(pageIndex, pageSize);
    }
  }

  //
  goPage(e) {
    this.refresh(parseInt(this.refs.pageTxt.value, 10));
  }

  render() {
    let disabled = ' fj-disabled',
      state = this.state,
      props = this.props,
      extra = {
        pageSizeChange: this.pageSizeChange,
        pageIndexBlur: this.pageIndexBlur,
        goPage: this.goPage,
        refresh: this.refresh,
        firstDisabled: '',
        prevDisabled: '',
        nextDisabled: '',
        lastDisabled: ''
      };
    let { className } = props;

    //
    if (state.pageCount <= 1) {  //
      extra.firstDisabled = disabled;
      extra.prevDisabled = disabled;
      extra.nextDisabled = disabled;
      extra.lastDisabled = disabled;
    }
    else if (state.pageIndex == 1) {  //
      extra.firstDisabled = disabled;
      extra.prevDisabled = disabled;
    }
    else if (state.pageIndex == state.pageCount) {  //
      extra.nextDisabled = disabled;
      extra.lastDisabled = disabled;
    }

    extra.classes = classNames({
      'fj-pagn': true,
      [className]: className
    });
    extra.wrap = c => this.wrap = c;

    return this.template(state, props, extra);
  }
}

registerFilter({
  clickBtn: function(fn, type) {
    let { pageIndex, pageCount } = this.data;

    switch(type){
      case 'first':
        return () => {
          if(pageIndex != 1) {
            fn(1);
          }
        };
      case 'prev':
        return () => {
          if(pageIndex > 1) {
            fn(pageIndex - 1);
          }
        };
      case 'next':
        return () => {
          if(pageIndex < pageCount) {
            fn(pageIndex + 1);
          }
        };
      case 'last':
        return () => {
          if(pageIndex != pageCount) {
            fn(pageCount);
          }
        };
      case 'index':
        return () => {
          if(this.index != pageIndex) {
            fn(this.index);
          }
        };
      default:
        return () => fn();
    }
  },
  isCurrentPage: function(no) {
    return parseInt(no, 10) == this.data.pageIndex ? '-c' : '';
  },
  showPartPage: function(no, type) {
    let { pageCount } = this.data;
    switch(type){
      case '1':  //<=4:++2+...+
        return no <= 4;
      case '2':  //>4,<=(n)-3:+...+2+ +2+...+
        return no > 4 && no <= pageCount - 3;
      case '3':  //>(n)- 3:+...+2++
        return no > pageCount - 3;
    }
  }
});

/**
 * 
 */
@registerTmpl({
  name: 'fj-PageCount',
  template: tmpl.pageCount
})
class PageCount extends Component {
  static defaultProps = {
    prefix: '',
    suffix: ''
  };

  render() {
    let {
      className,
      prefix,
      pageCount,
      count,
      pageSize,
      suffix, ...others } = this.props;
    
    const classes = classNames({
      'fj-pagn-part': true,
      [className]: className
    });
    
    //
    if(count != null && pageSize != null) {
      pageCount = getPageCount(count, pageSize);
    }
    
    return this.template({
      props: others,
      classes,
      prefix,
      pageCount,
      suffix,
      wrap: c => this.wrap = c
    });
  }
}

Pagination.PageCount = PageCount;

/**
 * 
 */
@registerTmpl({
  name: 'fj-PageDataCount',
  template: tmpl.pageDataCount
})
class PageDataCount extends Component {
  static defaultProps = {
    prefix: '',
    suffix: ''
  };

  render() {
    const {
      className,
      prefix,
      count,
      suffix, ...others } = this.props;
    
    const classes = classNames({
      'fj-pagn-part': true,
      [className]: className
    });
    
    return this.template({
      props: others,
      classes,
      prefix,
      count,
      suffix,
      wrap: c => this.wrap = c
    });
  }
}

Pagination.PageDataCount = PageDataCount;

/**
 * 
 */
@registerTmpl({
  name: 'fj-PageSize',
  template: tmpl.pageSize
})
class PageSize extends Component {
  static propTypes = {
    pageSize: PropTypes.number,
    pageSizes: PropTypes.array,
    setPageSize: PropTypes.oneOfType([
      PropTypes.bool,
      PropTypes.string
    ]),
    prefix: PropTypes.string,
    suffix: PropTypes.string,
    onChange: PropTypes.func
  };

  static defaultProps = {
    pageSize: 15,                           //
    pageSizes: [15, 30, 50],                //
    setPageSize: false,                     //
    prefix: '',
    suffix: ''
  };

  state = {
    pageSize: null
  };
  
  constructor(props) {
    super(props);

    this.state.pageSize = this.props.pageSize;
    this.pageSizeChange = this.pageSizeChange.bind(this);
  }

  componentWillReceiveProps (nextProps) {
    this.setState({
      pageSize: nextProps.pageSize
    });
  }

  pageSizeChange(e) {
    let props = this.props,
      pageSize = e.target.value;

    this.setState({
      pageSize
    }, () => {
      if(props.onChange) {
        props.onChange(pageSize);
      }
    });
  }

  render() {
    const {
      className,
      prefix,
      setPageSize,
      pageSize,
      pageSizes,
      suffix,
      onChange, ...others } = this.props;
    
    const classes = classNames({
      'fj-pagn-part': true,
      [className]: className
    });

    return this.template(this.state, {
      pageSizeChange: this.pageSizeChange,
      props: others,
      classes,
      prefix,
      setPageSize,
      pageSizes,
      suffix,
      wrap: c => this.wrap = c
    });
  }
}

Pagination.PageSize = PageSize;

export default Pagination;


