import { Component, PropTypes, Children } from 'react';
import { registerTmpl } from 'nornj';
import { isMobile } from '../../utils/browsers';
import { lazyDo } from '../../utils/delayOperate';
import { on, off } from '../../utils/domEvent';
import { bindThis } from '../../utils/common';

@registerTmpl({
  name: 'fj-Gesture',
  template: '<#cloneElem {props}>{children}</#cloneElem>'
})
export default class Gesture extends Component {
  static propTypes = {
    onTapStart: PropTypes.func,    //
    onTap: PropTypes.func,         //
    onPan: PropTypes.func,         //
    onTapEnd: PropTypes.func,      //
    onTapHold: PropTypes.func,     //
    onSwipe: PropTypes.func,       //
    onSwipeLeft: PropTypes.func,   //
    onSwipeRight: PropTypes.func,  //
    onSwipeTop: PropTypes.func,    //
    onSwipeBottom: PropTypes.func, //
    onScrollEnd: PropTypes.func,   //
    onPinch: PropTypes.func,       //
    onRotate: PropTypes.func,      //
    onShake: PropTypes.func        //
  };

  static defaultProps = {
    multiTapStart: false,  //tapStart
    preventScroll: true,   //
    preventClick: true,    //
    scrollElemV: null,     //()
    scrollElemH: null,     //()
    durationLimitH: 300,   //
    durationLimitS: 150,   //
    distanceLimitH: 30,    //
    distanceLimitV: 75,    //
    scrollMaxX: null,      //x,null
    scrollMaxY: null,      //y,null
    scrollMinX: 10,        //x,scrollMaxY
    scrollMinY: 5,        //y,scrollMaxX
    scalePinchClose: 0.05, //
    scalePinchOpen: 0.05,  //
    durationPinch: 100,    //
    durationRotate: 100,   //
    durationPan: null,     //
    rotationCcw: 5,        //
    rotationCw: 5,         //
    tapDuration: 350,      //
    tapLimitX: 0,
    tapLimitY: 0,
    holdLimitX: 1,         //x
    holdLimitY: 1,         //y
    shakeLimit: 0.23,      //
    shakeDuration: 100,    //
    shakeFreezeDelay: 800  //
  };

  constructor(props) {
    super(props);

    if (isMobile) {
      bindThis(this, [
        'touchStart',
        'touchMove',
        'touchEnd'
      ]);

      this.startPosX = 0;  //x
      this.lastPosX = 0;   //x
      this.startPosY = 0;  //y
      this.lastPosY = 0;   //y
      this.dx = 0;         //x
      this.dy = 0;         //y
      this.lastPinchTime = 0;  //
      this.lastScale = 0;  //
      this.lastPanTime = 0;//
      this.lastRotateTime = 0;  //
      this.lastRotation = 0;  //
      this.preventScroll = props.preventScroll;

      //
      if (props.onTapHold) {
        if (props.scrollElemV) {
          this.scrollElemV = props.scrollElemV;
        }
        else {
          this.scrollElemV = document.body;
        }
        if (!props.scrollElemH) {
          this.scrollElemH = this.scrollElemV;
        }
      }

      //
      this.supportShake = 'DeviceMotionEvent' in window;
      if (this.supportShake && this.props.onShake) {  //
        bindThis(this, ['deviceMotion']);

        this.lastShakeX = 0;
        this.lastShakeY = 0;
        this.lastShakeZ = 0;
        this.lastTime = 0;
        this.freezeShake = false;

        on("devicemotion", this.deviceMotion, window);
      }
    }
  }

  //
  touchStart(e) {
    let props = this.props,
      posAll = this.getTouchPos(e),
      pos = posAll[0],
      x = pos[0],
      y = pos[1],
      tl = posAll.length,
      ret = { e, x: x, y: y, fingers: tl },
      scrollElemV = null,
      scrollElemH = null;

    //
    this.tapFingers = tl;

    //
    this.startPosX = x;
    this.lastPosX = x;
    this.startPosY = y;
    this.lastPosY = y;

    if (tl == 1) {
      //
      this.duration = Date.now();
      this.isScroll = null;
      this.scale = null;
      this.lastScale = 0;
      this.isPinch = false;
      this.rotation = null;
      this.lastRotation = 0;
      this.isRotate = false;
      this.dx = 0;
      this.dy = 0;

      //
      if (props.onTapHold) {
        this.scrollElemTop = this.scrollElemV.scrollTop;
        this.scrollElemLeft = this.scrollElemH.scrollLeft;
      }

      props.onTapStart && props.onTapStart.call(this, ret);

      //,
      if (props.onTapHold) {
        this.timeoutH = lazyDo(() => {
          if (!this.isPinch && !this.isRotate && this.dx <= props.holdLimitX && this.dy <= props.holdLimitY && this.scrollElemTop == this.scrollElemV.scrollTop && this.scrollElemLeft == this.scrollElemH.scrollLeft) {
            ret.fingers = this.tapFingers;
            props.onTapHold.call(this, ret);
          }
        }, props.durationLimitH);
      }
    }
    else {
      if (tl == 2) {
        let pos1 = posAll[0],
          pos2 = posAll[1];

        //
        this.fingerDistS = this.getPosDistance(pos1, pos2);

        //
        this.fingerRotateS = this.getPosAngle(pos1, pos2);
      }

      //tapStart
      if (props.multiTapStart) {
        props.onTapStart && props.onTapStart.call(this, ret);
      }
    }
  }

  //
  touchMove(e) {
    let props = this.props,
      posAll = this.getTouchPos(e),
      pos = posAll[0],
      x = pos[0],
      y = pos[1],
      mx = props.scrollMaxX,
      my = props.scrollMaxY,
      ix = props.scrollMinX,
      iy = props.scrollMinY,
      tl = posAll.length,
      twoFingers = tl === 2 && (props.onPinch || props.onRotate);

    this.lastPosX = x;
    this.dx = this.lastPosX - this.startPosX;
    this.lastPosY = y;
    this.dy = this.lastPosY - this.startPosY;

    if (twoFingers) {  //
      e.preventDefault();
    }

    if (this.isScroll) {  //touchMove,
      return;
    }
    if (this.isScroll == null) {
      if (mx != null && Math.abs(this.dy) >= iy && Math.abs(this.dx) <= mx) {  //,
        this.isScroll = true;
        return;
      }
      if (my != null && Math.abs(this.dx) >= ix && Math.abs(this.dy) <= my) {  //,
        this.isScroll = true;
        return;
      }
    }

    //,
    if (this.preventScroll) {
      e.preventDefault();  //
    }
    this.isScroll = false;

    //
    let dir = this.getMoveDir(),
      curTime = Date.now();

    if (twoFingers) {
      let diffTime = curTime - this.lastPinchTime;

      //
      if (diffTime > props.durationPinch) {  //
        this.lastPinchTime = curTime;
        this.isPinch = true;

        //
        let scale = this.getPosScale(posAll[0], posAll[1]),
          dirS = null;

        if (scale != this.lastScale) {  //
          this.lastScale = scale;

          if (((scale < 1) && (scale % 1) < (1 - props.scalePinchClose)) || ((scale > 1) && (scale % 1) > props.scalePinchOpen)) {  //
            dirS = scale < 1 ? -1 : 1;
            this.scale = scale;

            this.preventScroll = true;  //
            props.onPinch && props.onPinch.call(this, { e, scale: scale, dir: dirS });
          }
        }
      }

      diffTime = curTime - this.lastRotateTime;

      //
      if (diffTime > props.durationRotate) {  //
        this.lastRotateTime = curTime;
        this.isRotate = true;

        //
        let rotation = this.getPosRotation(posAll[0], posAll[1]),
          dirS = null;

        if (rotation != this.lastRotation) {  //
          this.lastRotation = rotation;

          if (((rotation < 1) && (-1 * (rotation) > props.rotationCcw)) || ((rotation > 1) && (rotation > props.rotationCw))) {  //
            dirS = rotation < 1 ? -1 : 1;
            this.rotation = rotation;

            this.preventScroll = true;  //
            props.onRotate && props.onRotate.call(this, { e, rotation: rotation, dir: dirS });
          }
        }
      }
    }

    //
    if (props.durationPan == null || curTime - this.lastPanTime > props.durationPan) {
      this.lastPanTime = curTime;
      props.onPan && props.onPan.call(this, { e, x: x, y: y, dx: this.dx, dy: this.dy, dirX: dir[0], dirY: dir[1], fingers: tl, touchId: this.getTouchIndex(e) });
    }
  }

  //
  touchEnd(e) {
    let props = this.props,
      isScroll = this.isScroll;

    if (e.touches.length > 0) {
      return;
    }

    //
    if (this.timeoutH) {
      clearTimeout(this.timeoutH);
      this.timeoutH = null;
    }

    if (this.isScroll) {
      props.onScrollEnd && props.onScrollEnd.call(this, { e });
      return;
    }

    let dir = this.getMoveDir(),  //
      ret = { e, x: this.lastPosX, y: this.lastPosY, dx: this.dx, dy: this.dy, dirX: dir[0], dirY: dir[1], fingers: this.tapFingers },  //
      gtH = Math.abs(this.dx) > props.distanceLimitH,
      gtV = Math.abs(this.dy) > props.distanceLimitV,
      cTime = Date.now(),
      duration = cTime - this.duration;

    //
    if (duration <= props.durationLimitS) {
      let dirS = '';
      if (gtH) {
        if (dir[0] == 'l') {
          dirS += 'l';
          props.onSwipeLeft && props.onSwipeLeft.call(this, ret);
        }
        else {
          dirS += 'r';
          props.onSwipeRight && props.onSwipeRight.call(this, ret);
        }
      }
      if (gtV) {
        if (dir[1] == 't') {
          dirS += 't';
          props.onSwipeTop && props.onSwipeTop.call(this, ret);
        }
        else {
          dirS += 'b';
          props.onSwipeBottom && props.onSwipeBottom.call(this, ret);
        }
      }
      if (gtH || gtV) {
        ret.dirS = dirS;  //
        props.onSwipe && props.onSwipe.call(this, ret);
      }
    }

    //,
    if (duration <= props.tapDuration && this.dx <= props.tapLimitX && this.dy <= props.tapLimitY) {
      if (props.preventClick) {
        e.preventDefault();
      }

      props.onTap && props.onTap.call(this, ret);
    }

    //
    if (this.scale != null) {
      ret.scale = this.scale;
    }

    //
    if (this.rotation != null) {
      ret.rotation = this.rotation;
    }

    props.onTapEnd && props.onTapEnd.call(this, ret);
  }

  //
  deviceMotion(e) {
    let props = this.props,
      acceleration = e.accelerationIncludingGravity,  //
      curTime = Date.now(),
      diffTime = curTime - this.lastTime,  //
      ret = { e };

    //
    if (diffTime > props.shakeDuration) {
      this.lastTime = curTime;
      let x = acceleration.x,
        y = acceleration.y,
        z = acceleration.z,
        speed = Math.abs(x + y + z - this.lastShakeX - this.lastShakeY - this.lastShakeZ) / diffTime;

      if (speed > props.shakeLimit) {  //
        if (!this.freezeShake) {
          this.freezeShake = true;  //,,
          props.onShake(ret);
        }

        lazyDo(() => {
          this.freezeShake = false;
        }, props.shakeFreezeDelay, 'gesture_shake', this);
      }

      this.lastShakeX = x;
      this.lastShakeY = y;
      this.lastShakeZ = z;
    }
  }

  //
  getTouchPos(e) {
    let touches = e.touches,
      osLeft = this.wrapper.offsetLeft,
      osTop = this.wrapper.offsetTop,
      ts = null,
      ret = [];

    for (let i = 0, l = touches.length; i < l; i++) {
      ts = touches[i];
      ret[ret.length] = [ts.pageX - osLeft, ts.pageY - osTop];  //:xy
    }

    return ret;
  }

  //
  getMoveDir() {
    let dirX, dirY;
    if (this.dx > 0) {
      dirX = "r";
    }
    else {
      dirX = "l";
    }
    if (this.dy > 0) {
      dirY = "b";
    }
    else {
      dirY = "t";
    }

    return [dirX, dirY];
  }

  //
  getTouchIndex(e) {
    let targetTouch = e.targetTouches[0],
      touches = e.touches;

    for (let i = 0, l = touches.length; i < l; i++) {
      if(touches[i].identifier === targetTouch.identifier) {
        return i;
      }
    }
  }

  //
  getPosDistance(pos1, pos2) {
    let x = pos1[0] - pos2[0],
      y = pos1[1] - pos2[1];

    return Math.sqrt(
        Math.pow(x, 2) + Math.pow(y, 2)
    );
  }

  //
  getPosScale(pos1, pos2) {
    return this.getPosDistance(pos1, pos2) / this.fingerDistS;
  }

  //
  getPosAngle(pos1, pos2) {
    let x = pos1[0] - pos2[0],
      y = pos1[1] - pos2[1];

    return Math.atan2(y, x) * 180 / Math.PI;
  }

  //
  getPosRotation(pos1, pos2) {
    return this.getPosAngle(pos1, pos2) - this.fingerRotateS;
  }

  componentWillUnmount() {
    if (isMobile && this.supportShake && this.props.onShake) {
      off("devicemotion", this.deviceMotion, window);
    }
  }

  render() {
    return this.template({
      props: isMobile ? {
        ref: c => this.wrapper = c,
        onTouchStart: this.touchStart,
        onTouchMove: this.touchMove,
        onTouchEnd: this.touchEnd,
        onTouchCancel: this.touchEnd
      } : null
    }, this.props);
  }
}


